	module.exports = {"data":{"markdownRemark":{"html":"<p>Signal is the average Reputation per report. Reputation is gained or lost each time a report is closed, making Signal an aggregate representation of report validity. Self closed reports and duplicate reports are not included in Signal calculations.</p>\n<p>Impact is the average Reputation per bounty. Reputation is gained based on the relative size of the awarded bounty, making Impact an aggregate representation of report severity.</p>\n<p>Signal provides a means of identifying hackers who've had consistently valid reports while Impact highlights those with the greatest severity. Both signal and Impact will help you benefit through an improved understanding of your performance relative to your peers.</p>\n<h3>Signal and Impact Facts</h3>\n<ul>\n<li>Signal and Impact are only calculated when there are more than 3 closed reports or 3 bounties respectively.</li>\n<li>Signal is measured on a scale from -10 to 7, corresponding to Reputation changes for triage states ranging from \"Spam\" to \"Resolved.\"</li>\n<li>Self closed and duplicate reports don't impact your Signal. </li>\n<li>Impact is measured on a scale from 0 to 50, and it corresponds to Reputation gains calculated by bounty levels awarded.</li>\n<li>Impact is only calculated for reports submitted to programs that offer bounties.</li>\n<li>HackerOne displays a hacker's percentile rank for their Signal and Impact relative to other hackers on the platform.</li>\n</ul>\n<h3>Calculation Example</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/docs.hackerone.com/static/signal-impact-1-3051ed1036eb4b13b30016a1a0ef979a-552db.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 74.4186046511628%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"signal-impact-1\"\n        title=\"\"\n        src=\"/docs.hackerone.com/static/signal-impact-1-3051ed1036eb4b13b30016a1a0ef979a-e7ffa.png\"\n        srcset=\"/docs.hackerone.com/static/signal-impact-1-3051ed1036eb4b13b30016a1a0ef979a-49813.png 125w,\n/docs.hackerone.com/static/signal-impact-1-3051ed1036eb4b13b30016a1a0ef979a-dc8a5.png 250w,\n/docs.hackerone.com/static/signal-impact-1-3051ed1036eb4b13b30016a1a0ef979a-e7ffa.png 500w,\n/docs.hackerone.com/static/signal-impact-1-3051ed1036eb4b13b30016a1a0ef979a-dda24.png 750w,\n/docs.hackerone.com/static/signal-impact-1-3051ed1036eb4b13b30016a1a0ef979a-c5f18.png 1000w,\n/docs.hackerone.com/static/signal-impact-1-3051ed1036eb4b13b30016a1a0ef979a-552db.png 1462w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Alice and Bob have the exact same amount of Reputation (241 points). But Alice has a higher Signal (4.12 > 1.35), which indicates having a better track record of submitting valid reports. Alice also has a higher Impact (15.83 > 9.50) and therefore, a higher chance of submitting an important report.</p>\n<p>This is valuable information for both of them. Alice will see that she is doing quite well with her current trajectory, while Bob will see that others are more accurate about their reports and can adjust his reporting processes accordingly.</p>","frontmatter":{"path":"/hackers/signal-and-impact","title":"Signal and Impact"}}},"pathContext":{}}

